<template>
  <div class="index mescroll" id="list">
    <div class="list-data" id="listData">
      <div class="roof" v-if="roofShow &&false">
        <div class="roof-icon" @click="roofShow = false">
          <img src="../../assets/img/icon/close.png" alt crossOrigin='Anonymous' />
        </div>
        <div class="roof-left">
          <img :src="$config.logo" alt />
        </div>
        <div class="roof-mid">
          <p class="name">{{$config.name}}</p>
          <p class="text">{{$config.name}}欢迎您</p>
        </div>
        <div class="roof-right">
          <div class="btn" @click="show = true">关注公众号</div>
        </div>
      </div>
      <div class="flex1">
        <div class="flex1-2">
          <form action="/">
            <van-search v-model="value" show-action action-text="搜索" @cancel="onSearch" placeholder="搜索" background="#fff" @search="onSearch" @blur="blur" />
          </form>
        </div>
        <div class="flex1-1">
          <img src="../../assets/img/icon/qrcode.png" @click="show = true">
        </div>
      </div>
      <div class="banner">
        <van-swipe :autoplay="3000" indicator-color="white">
          <van-swipe-item v-for="(item,index) in picList" :key="index">
            <img :src="$utils.dealImg(item.bimg)" alt />
          </van-swipe-item>
        </van-swipe>
      </div>
      <div class="top">
        <div class="flex">
          <van-notice-bar :text="notice" left-icon="volume" />
        </div>
        <div class="index-classify" v-if="false">
          <grid :grid-array="classifyList" width="55px" height="55px" font-color="#071ff7" borderRadius="50%" @goRouter="goRouter" />
        </div>
      </div>
      <div class="mid">
        <van-tabs v-model="active" :line-width="20" :line-height="3" :swipe-threshold="6"  :title-active-color="'#071ff7'" @click="onClick">
          <van-tab v-for="(item,index) in goodsClassifyList" :key="index" :title="item.cateName"></van-tab>
        </van-tabs>
        <div class="list">
          <div class="item" v-for="(item,index) in goodsList" :key="index" @click="goDetail(item)">
            <div class="topImage">
            <img :src="$utils.dealImg(item.gimg)" alt />
            </div>
            <div class="bottomText">
              <p class="title">{{item.gname}}</p>
              <div class="labels">
                <div class="label">数量有限买完为止</div>
                <div class="label" v-if="item.gfanj">送{{item.gfanj}}积分</div>
              </div>
              <div class="bottom">
                <div class="left">
                   <div class="price1">
                    <span class="label">优惠价</span>
                    <span class="font">¥</span>
                    <span class="num">{{item.goutPrice?item.goutPrice.toFixed(2):'0.00'}}</span>
                  </div>
                  <div class="price">
                    <span class="label">市场价</span>
                    <span class="font">¥</span>
                    <span class="num">
                      <del>{{item.gmarketPrice?item.gmarketPrice.toFixed(2):'0.00'}}</del>
                    </span>
                  </div>
                 </div>
              </div>
            </div>
          </div>
      </div>
      <div class="line" style="border-bottom:0"></div> 

    </div>
 
    <van-popup v-model="show">
      <div class="popup-mid" @click="show = false">
        <img :src="qrcode" alt />
      </div>
    </van-popup>
  </div>
  </div>
</template>
<script>
import { Tab, Tabs, NoticeBar, Search, Swipe, SwipeItem, Popup, Panel  } from "vant";
import MeScroll from "mescroll.js";
import {
  getBanner,
  getIndexMenu,
  getIndexGoodsList,
  getShopNavigation,
  getPersonalInfo,
  getFilgain
} from "@/api";
import Grid from "@/components/Grid";
export default {
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [NoticeBar.name]: NoticeBar,
    [Search.name]: Search,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [Popup.name]: Popup,
    Grid,
    Panel
  },
  data() {
    return {
      classifyList: [],
      goodsClassifyList: [],
      picList: [],
      value: "",
      active: 0,
      goodsList: [],
      notice: this.$config.name + "欢迎您",
      mescroll: null,
      show: false,
      qrcode: "",
      roofShow: true,
      userLevel: 1,
      filgainList: [],
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    bannerClick(item) {
      console.log(item);
      if (this.userLevel > 2) {
        this.$router.push({
          name: "guandanRegister",
        });
      } else {
        this.$router.push({
          name: "guandanRegister",
        });
      }
    },
    creatCanvas() {
      var that = this;
      var wid1 = 900 * 0.29; //大 右
      var hei1 = 1100 * 0.37; //大 下
      var canvas = document.createElement("canvas");
      var ctx = canvas.getContext("2d");
      var ratio = this.getPixelRatio(ctx) * 0.5;
      canvas.width = 750 * ratio;
      canvas.height = 1100 * ratio;
      ctx.rect(0, 0, canvas.width * ratio, canvas.height * ratio);
      ctx.fillStyle = "#fff";
      ctx.fill();
      var img = new Image();
      img.src = require("../../assets/img/bg.jpg");
      img.crossOrigin = "Anonymous";
      img.onload = function() {
        ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
        var img1 = new Image();
        img1.src = that.$config.qrCode;
        img1.onload = function() {
          ctx.drawImage(
            img1,
            (canvas.width - 400 * ratio) / 2,
            (canvas.height - 450 * ratio) / 2,
            400 * ratio,
            400 * ratio
          );
          ctx.fillStyle = "#fff";
          ctx.font = "3.5em normal 黑体";
          ctx.textAlign = "center";
          ctx.fillText(
            "长按识别图中二维码，关注公众号",
            canvas.width / 2,
            (canvas.height - 450 * ratio) / 2 + 435 * ratio
          );
          // var base64 = canvas.toDataURL("image/jpeg", 1); //"image/png" 这里注意一下,参数里面不要带"image/png" 之前网上代码还有这个参数一直报错
          // that.qrcode = base64;
        };
      };
    },
    circleImg(ctx, img, w, h, x, y) {
      ctx.save();
      ctx.beginPath(); //开始绘制
      ctx.arc(w / 2 + x, h / 2 + y, w / 2, 0, Math.PI * 2, false);
      ctx.clip(); //画好了圆 剪切  原始画布中剪切任意形状和尺寸。一旦剪切了某个区域，则所有之后的绘图都会被限制在被剪切的区域内 这也是我们要save上下文的原因
      ctx.drawImage(img, x, y, w, h); // 推进去图片，必须是https图片
      ctx.restore(); //恢复之前保存的绘图上下文 恢复之前保存的绘图上下午即状态 还可以继续绘制
    },
    getPixelRatio(context) {
      var backingStore =
        context.backingStorePixelRatio ||
        context.webkitBackingStorePixelRatio ||
        context.mozBackingStorePixelRatio ||
        context.msBackingStorePixelRatio ||
        context.oBackingStorePixelRatio ||
        context.backingStorePixelRatio ||
        1;
      return (window.devicePixelRatio || 1) / backingStore;
    },
    goRouter(item) {
      this.$router.push({
        name: "goodsList",
        query: { id: item.id },
      });
    },
    onSearch() {
      this.mescroll.scrollTo(0, 0);
      this.mescroll.resetUpScroll();
    },
    onClick() {
      this.value = "";
      // this.mescroll.scrollTo(0, 0);
      this.mescroll.resetUpScroll();
    },
    goDetail(item) {
      this.$router.push({
        name: "goodsDetail",
        query: { id: item.gcode, title: item.gname },
      });
    },
    getData() {
      getBanner().then(res => {
        if (res.code == 0) {
          this.picList = res.data;
        } else {
          this.$toast.fail(res.msg);
        }
      });
      getIndexMenu().then(res => {
        if (res.code == 0) {
          this.goodsClassifyList = res.data;
          setTimeout(() => {
            this.startMescroll();
          }, 600);
        } else {
          this.$toast.fail(res.msg);
        }
      });
      getPersonalInfo().then(res => {
        if (res.code == 0) {
          this.userLevel = parseInt(res.data.userLevel);
        } else {
          this.$toast.fail(res.msg);
        }
      });
      getFilgain().then(res => {
        if (res.code == 0) {
           this.filgainList = res.data;
        } else {
          this.$toast.fail(res.msg);
        } 
      }); 
    },
    loadData(pageNum, pageSize, successCallback, errorCallback) {
      let $this = this;
      let dataObj = {
        cateCode: this.goodsClassifyList[this.active].cateCode,
        viewName: this.value,
        pageNum: pageNum,
        pageSize: pageSize,
      };
      getIndexGoodsList(dataObj)
        .then(res => {
          if (res.code == 0) {
            successCallback && successCallback(res.data);
          } else {
            $this.$toast.fail(res.errorMsg);
          }
        })
        .catch(res => {
          errorCallback && errorCallback();
          console.log(res);
        }); 
    },
    startMescroll() {
      this.mescroll = new MeScroll("list", {
        //在vue的mounted生命周期初始化mescroll,确保此处配置的id能够被找到
        up: {
          callback: this.upCallback, //上拉回调
          //以下参数可删除,不配置
          isBounce: false, //此处禁止ios回弹,解析(务必认真阅读,特别是最后一点): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //可配置每页8条数据,默认10
          empty: {
            //配置列表无任何数据的提示
            warpId: "listData",
            icon: require("@/assets/img/nodata.png"),
            tip: "暂无数据",
            //						  	btntext : "去逛逛 >" ,
            //						  	btnClick : function() {
            //						  		alert("点击了去逛逛按钮");
            //						  	}
          },
          noMoreSize: 10,
          scrollbar: { use: false },
          htmlNodata: '<p class="upwarp-nodata">-- 无更多数据 --</p>',
          lazyLoad: {
            use: true, // 是否开启懒加载,默认false
          },
        },
      });
    },
    //上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
    upCallback: function(page) {
      //联网加载数据
      let $this = this;
      $this.loadData(
        page.num,
        page.size,
        function(curPageData) {
          //curPageData=[]; //打开本行注释,可演示列表无任何数据empty的配置
          //如果是第一页需手动制空列表 (代替clearId和clearEmptyId的配置)
          console.log(curPageData);
          if (page.num == 1) $this.goodsList = [];
          if (curPageData.list) {
            curPageData.list.map(item => {
              $this.goodsList.push(item);
            });
          }
          //联网成功的回调,隐藏下拉刷新和上拉加载的状态;
          //mescroll会根据传的参数,自动判断列表如果无任何数据,则提示空;列表无下一页数据,则提示无更多数据;
          //   console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.rows.length+", $this.goodsList.length==" + $this.pdlist.length);
          console.log(page);

          //方法二(推荐): 后台接口有返回列表的总数据量 totalSize
          $this.mescroll.endBySize(
            curPageData.list ? curPageData.list.length : 0,
            curPageData.total
          ); //必传参数(当前页的数据个数, 总数据量)
        },
        function() {
          //联网失败的回调,隐藏下拉刷新和上拉加载的状态;
          $this.mescroll.endErr();
        }
      );
    },
  },
  mounted() {
    document.title = this.$config.name;
    this.getData();
    this.creatCanvas();
  },
};
</script>
<style lang="scss" scoped>
