        display: flex;
        .topImage {
          display: inline-block;
          img {
            width: 180px;
            height:100%;
            // height: calc(120px / 1.316);
          }
        }
        .bottomText {
          display: inline-block;
          .title {
            margin-left: 15px;
            padding-bottom:5px;
            font-size: 20px;
            line-height: 20px;
            max-height: 50px; 
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            overflow: hidden;
            color: gold;
          }
          .labels {
            margin-left: 15px;
            margin-top: 15px;
            .label {
              display: inline-block;
              font-size: 11px;
              margin-right: 10px;
              padding: 3px 8px;
              border-radius: 5px;
              border: 1pt solid #f70707;;
              color: #f70707;
            }
          }
          .bottom {
            color: $base-color;
            font-size: 12px;
            line-height: 30px;
            width: 100%;
            display: flex;
            align-items: center;
            margin: 10px 0 0;
            padding-top: 10px;
            .left {
              margin-top: -6px;
              display: flex;
           
            .line{
              margin-top: 18px;
              //margin-left: 6px;
              width:93vw;
            }
            .price {
              display: inline-block;
              padding-left: 5px;
              
              .label {
                font-size: 10px;
                 color: #000000;
                background: #dae6fd;
                padding: 3px 5px;
                border-radius: 5px;
                margin-right: 5px;
                box-sizing: border-box;
                border:0;
              }
              .font {
                font-size: 10px;
                 color: white;
                margin-right: 3px;
              }
              .num {
                font-weight: bold;
                font-size: 12px;
                color: white;
              }
            }
            .price1 {
              display: inline-block;
              margin-left: 15px;
              .label {
                font-size: 10px;
               color: #f70707;
                border: 1pt solid #f70707;
                padding: 3px 5px;
                border-radius: 5px;
                margin-right: 5px;
                box-sizing: border-box;
              }
              .font {
                font-size: 12px;
               color: #f70707;
              }
              .num {
                font-weight: bold;
                font-size: 12px;
               color: #f70707;
              }
            } }
          }
        }
      }
    }
    
  }
  .goodNews{
      width: 98vm;
      margin-top: 10px;
      margin-bottom: 10px;  
      .van-cell__title{
        font-size: 14px;
        margin-left: -10px;
        color: #f7ab07; 
      }
      /deep/.van-panel__header{
        background:#253238;
      }
      .time{
        width: 98vm;
        line-height: 40px;
        // display: flex;
        font-size: 12px; 
